html {
  scroll-behavior: smooth;
}

:root {
  --dark-color: rgb(255, 142, 71);
  --light-color: rgb(255, 192, 92);
}

.App {
  display: flex;
  flex-direction: column;
  background-color: white;
  margin: 0 !important;
  padding: 0 !important;
}

.fadeInContainer {
  animation: fadeIn 1s;
}

@keyframes fadeIn {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

/* SHARED STYLES */
.filmSpots, .background, .grain, .filmScratchInner, .filmScratchOuter {
  z-index: 1;
  pointer-events: none; /* allow click thru */
}

.filmSpots, .background, .filmScratchInner, .filmScratchOuter {
  position: fixed;
  height: 100vh;
  width: 100vw;
}
/* END SHARED STYLES */

