/* Film damage effect */
/* Image from T. H. Doan, check out his codepen! */
.filmSpots {
  display: none;
  background: url('//res.cloudinary.com/thdoan/image/upload/v1509581279/countdown/film_grain-small.gif') no-repeat;
  background-size: 100% 100%;
  opacity: 0.15;
}

.filmSpots.active {
  display: block;
}

/* Creating of the old time-y lighting effect */
.background {
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 55%, rgba(178, 146, 113, 0.5) 100%);
  /*background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 55%, rgba(149, 113, 178, 0.3) 100%);*/
  opacity: 0.8;
}

.background.active {
  /*background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 50%, rgba(178, 146, 113, 0.38) 100%);*/
  background: radial-gradient(ellipse at center, rgba(255, 255, 255, 0) 45%, rgba(178, 146, 113, 0.7) 95%);

  -webkit-animation: ease flicker 2s infinite;
  animation: ease flicker 2s infinite;
  opacity: 1;
}

@keyframes flicker {
  0% { opacity: 0.3; }
  10% { opacity: 0.5; }
  20% { opacity: 0.35; }
  30% { opacity: 0.6; }
  40% { opacity: 0.7; }
  50% { opacity: 0.4; }
  60% { opacity: 0.85; }
  70% { opacity: 0.7 }
  80% { opacity: 0.6; }
  90% { opacity: 0.25; }
  100% { opacity: 0.5; }
}

/* Creation of grainy effect in the background */
.grain {
  position: fixed;
  height: 200vh;
  width: 200vw;
  top: -50%;
  left: -50%;

  opacity: 0.8;
  background-image: url(data:image/png;base64,);
}

.grain.active {
  -webkit-animation: grain 4s steps(6) infinite;
  animation:grain 4s steps(6) infinite;

  background-image: url(data:image/png;base64,);
  opacity: 1;
}

@keyframes grain {
  0%, 100% {transform: translate(0,0);}
  10% {transform: translate(-5%,-10%);}
  30% {transform: translate(3%,-15%);}
  50% {transform: translate(12%,9%);}
  70% {transform: translate(9%,4%);}
  90% {transform: translate(-1%,7%);}
}

/* Creating of film scratches/lines effect. */
/* Thanks to Brad Colthurst for his codepen! */
.filmScratchInner:after, .filmScratchOuter:after {
  content: '';
  width: 120%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  padding-left: 100px;
  opacity: .03;
  background-image: url("data:image/png;base64,");
  /*background-image: url("data:image/png;base64,");*/
  /*background-image: url("data:image/png;base64,");*/
}

.filmScratchInner.active:after, .filmScratchOuter.active:after {
  -webkit-animation: scratch 0.45s steps(1) infinite;
  animation: scratch 0.45s steps(1) infinite;
}

.filmScratchInner.active:after {
  left: 30%;
  -webkit-animation: innerScratch 2s infinite;
  animation: innerScratch 2s infinite;
}

@keyframes scratch {
  0%, 100% {
    transform: translateX(0);
    opacity: 0.150;
  }
  10% { transform: translateX(-1%); }
  20% { transform: translateX(1%); }
  30% {
    transform: translateX(-2%);
    opacity: 0.18;
  }
  40% { transform: translateX(3%); }
  50% {
    transform: translateX(-3%);
    opacity: 0.10;
  }
  60% { transform: translateX(8%); }
  70% { transform: translateX(-3%); }
  80% {
    transform: translateX(10%);
    opacity: 0.04;
  }
  90% { transform: translateX(-2%); }
}

@keyframes innerScratch {
  0% {
    transform: translateX(0);
    opacity: 0.16;
  }
  10% { transform: translateX(-1%); }
  20% { transform: translateX(1%); }
  30% { transform: translateX(-2%); }
  40% { transform: translateX(3%); }
  50% {
    transform: translateX(-3%);
    opacity: 0.12;
  }
  60% { transform: translateX(8%); }
  70% { transform: translateX(-3%); }
  80% {
    transform: translateX(10%);
    opacity: 0.06;
  }
  90% { transform: translateX(20%); }
  100% { transform: translateX(30%); }
}

/* == CUSTOM SCROLLBAR == */
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #A69E95;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #6D6862;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #4C4844;
}